.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 15px;
    padding-right: 15px;
    height: 60px;
    background: #FFFFFF;
    box-shadow: 0 0 40px rgba(29, 33, 38, 0.13), 0 0 2px rgba(29, 33, 38, 0.1);
}
.header>img {
    cursor: pointer;
}

.header h1 {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
}